import { Button } from "@repo/ui/components/button";

export default function Page() {
  return (
    <div className="flex min-h-screen flex-col items-center justify-center gap-8 p-10 bg-background text-foreground">
      <div className="text-center space-y-2">
        <h1 className="text-4xl font-bold tracking-tight text-primary">
          Design System Start
        </h1>
        <p className="text-muted-foreground">
          Next.js 15 + Tailwind v4 + Shadcn UI
        </p>
      </div>
      
      <div className="flex flex-wrap items-center justify-center gap-4 p-8 border rounded-xl shadow-sm bg-card">
        <Button>Default Button</Button>
        <Button variant="destructive">Destructive</Button>
        <Button variant="outline">Outline</Button>
        <Button variant="secondary">Secondary</Button>
        <Button variant="ghost">Ghost</Button>
        <Button variant="link">Link</Button>
      </div>
    </div>
  );
}